* {
	margin: 0;
	padding: 0;
}

::-webkit-scrollbar {
	width: 0;
}
html,
body {
	height: 100vh;
	background-image: url("../static/imgs/galaxy-background.jpg");
}

body {
	overflow-x: hidden;
}

.webgl {
	display: flex;
	position: absolute;
	top: 0;
	left: 0;
	outline: none;
	z-index: 1;
}

@media (max-width: 800px) {
	.webgl {
		display: none;
	}
}

.container {
	max-width: 1280px;
	padding: 0 1.5rem;
	margin: auto;
	overflow: hidden;
}

#header-btn {
	margin-top: 25rem;
}

#main-nav {
	display: flex;
	justify-content: space-between;
	padding-top: 1rem;
}
#main-nav ul {
	display: flex;
}
#main-nav li {
	padding: 1rem 1.5rem;
}
#main-nav #logo-anchor {
	border: none;
}
#main-nav a {
	text-decoration: none;
	text-transform: uppercase;
	color: #fff;
	border-bottom: 3px transparent solid;
	padding-bottom: 0.5rem;
	transition: border-color 0.5s;
}
#main-nav a:hover {
	border-color: #ccc;
}
#main-nav a.current {
	border-color: #ffbc00;
}

#logo {
	width: 70px;
	height: 70px;
	color: #fff;
	text-transform: uppercase;
}
#logo:hover {
	transform: scale(1.2);
	transition: 0.3s;
}

/*-------------------------------------- COPIED =---------------------------- */

.container {
	max-width: 1280px;
	padding: 0 1.5rem;
	margin: auto;
	overflow: hidden;
}

.btn-main,
.btn-light,
.btn-dark {
	display: inline-block;
	padding: 0.8rem 2rem;
	transition: all 0.5s;
	border: none;
	cursor: pointer;
}

.btn-main {
	margin-top: 20rem;
	color: #333;
	background-color: #ffbc00;
}

.btn-light {
	color: #333;
	background-color: #f4f4f4;
}

.btn-dark {
	color: #f4f4f4;
	background-color: #333;
}

button[class^="btn-"]:hover,
a[class^="btn-"]:hover,
input[class^="btn-"]:hover {
	background-color: #ffbc00;
	transform: scale(1.1);
	color: black;
	font-weight: bold;
}

.bg-main {
	background: #ffbc00;
}

.bg-dark {
	background: rgba(2, 0, 31, 0.781);
}

.bg-light {
	background: #f4f4f4;
}

.bg-main {
	background: #ffbc00;
}

.lead {
	font-size: 1.3rem;
	margin-bottom: 2rem;
	color: white;
}

.text-center {
	text-align: center;
}

.py-1 {
	padding: 1rem 0;
}

.py-2 {
	padding: 2rem 0;
}

.py-3 {
	padding: 3rem 0;
}

.py-4 {
	padding: 4rem 0;
}

.my-1 {
	margin: 1rem 0;
}

.my-2 {
	margin: 2rem 0;
}

.my-3 {
	margin: 3rem 0;
}

.my-4 {
	margin: 4rem 0;
}

.section-title {
	font-size: 2rem;
	display: block;
	padding-bottom: 0.5rem;
	text-align: center;
	text-transform: uppercase;
	color: white;
}

.bottom-line {
	height: 2px;
	width: 3rem;
	background: #ffbc00;
	display: block;
	margin: 0 auto 1rem auto;
}

.items {
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.3);
	grid-gap: 1rem;
}

.item {
	position: relative;
	background: #ffbc00;
	overflow: hidden;
}
.item::after {
	content: "";
	position: absolute;
	display: block;
	background: inherit;
	opacity: 0.9;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	transform: scale(2) translateX(-75%) translateY(-75%) rotate(-28deg);
	transition: transform 3s cubic-bezier(0.2, 1, 0.3, 1);
}
.item:hover::after {
	transform: scale(2) translateX(0) translateY(0) rotate(-28deg);
}
.item:hover .item-image {
	transform: scale(1.2);
	transition: 0.5s ease-in-out;
}
.item:hover .item-text {
	opacity: 1;
	transform: translateY(0);
}
.item-image {
	height: auto;
	transform: translateY(0);
	display: block;
	transition: transform 750ms cubic-bezier(0.2, 1, 0.3, 1);
}
.item-image:before {
	content: "";
	display: block;
	padding-top: 75%;
	overflow: hidden;
}
.item-image img {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: auto;
	line-height: 0;
}
.item-text {
	position: absolute;
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;
	opacity: 0;
	text-align: center;
	z-index: 1;
	color: black;
	transform: translateY(-20%);
	transition: opacity 500ms cubic-bezier(0.2, 1, 0.3, 1),
		transform 500ms cubic-bezier(0.2, 1, 0.3, 1);
	transition-delay: 300ms;
}
.item-text-wrap {
	width: 100%;
	position: absolute;
	top: 50%;
	transform: translateY(-50%);
}
.item-text-title {
	font-size: 2rem;
	padding: 0 1rem;
	margin: 5px 0 0 0;
}
.item-text-category {
	text-transform: uppercase;
	font-size: 1.2;
	opacity: 0.7;
	margin: 0;
}

