#header-home {
	height: 100vh;
	color: #fff;
	z-index: 2;
	position: relative;
}
#header-home .header-content {
	text-align: center;
	padding-top: 10%;
}
#header-home .header-content h1 {
	font-size: 4rem;
	line-height: 1.2;

	text-transform: uppercase;
}

#header-inner {
	height: 5.5rem;
	border-bottom: 3px solid #ffbc00;
}
#home-a {
	background: rgba(2, 0, 31, 0.781);
	color: white;
}

#home-a .specials {
	margin-top: 3rem;
	display: grid;
	grid-gap: 1rem;
	grid-template-columns: repeat(4, 1fr);
}
#home-a .specials .fas {
	color: #ffbc00;
	padding-bottom: 0.4rem;
}
#home-b {
	margin-top: 2rem;
	background: rgba(2, 0, 31, 0.781);
	color: white;
}
#home-b .stats {
	display: grid;
	grid-template-columns: repeat(3, 1fr);
}
#home-b .stats li {
	line-height: 2;
}
#home-b .stats li.stats-title {
	font-size: 1.5rem;
}
#home-b .stats li.stats.number {
	font-size: 2rem;
}
#home-b .stats div {
	padding: 3rem 0;
}
#home-b .stats div:nth-child(odd) {
	background: #02000080;
}
#home-b .stats div:nth-child(even) {
	background: #ffbb00cc;
}
#home-c {
	background: rgba(2, 0, 31, 0.781);
	color: white;
}
#home-c .process {
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	grid-gap: 1.5rem;
	text-align: center;
}
#home-c .process-step {
	position: absolute;
	top: 0;
	right: 0;
	font-size: 28px;
	background: #ffbc00;
	border-radius: 50%;
	height: 15px;
	width: 15px;
	line-height: 15px;
	padding: 1rem;
	transition: all 1s;
}
#home-c .process-icon {
	border-radius: 50%;
	background: #333;
	color: white;
	padding: 2rem;
	width: 70px;
	height: 70px;
	line-height: 70px;
	text-align: center;
	position: relative;
	transition: all 1s;
}
#home-c .process-icon:hover {
	background: #ffbc00;
	width: 90px;
	height: 90px;
	line-height: 90px;
}
#home-c .process-icon:hover .process-step {
	background: #333;
}

#about-a .about-info {
	color: white;
	display: grid;
	grid-template-areas:
		"bioimage bio bio"
		"st1 st2 st3";
	grid-gap: 1.2rem;
}
#about-a .about-info h4 {
	font-size: 1.4rem;
	margin: 1rem;
}

#about-a .bio-image {
	grid-area: bioimage;
}

#about-a .bio {
	grid-area: bio;
	border-left: 3px solid #ffbc00;
	padding: 1rem;
	font-size: 1.3rem;
}

#about-a .step-1 {
	grid-area: st1;
}

#about-a .step-2 {
	grid-area: st2;
}

#about-a .step-3 {
	grid-area: st3;
}

#about-a .step-1 .fas,
#about-a .step-2 .fas,
#about-a .step-3 .fas {
	color: #ffbc00;
	margin: 1rem -0.4rem;
}

#about-b h2,
#about-b h4 {
	color: white;
}

#about-b .progress {
	overflow: hidden;
	height: 20px;
	background: #ccc;
	border-radius: 5px;
	margin-bottom: 0.6 rem;
}
#about-b .progress div {
	height: 100%;
	color: white;
	text-align: center;
	background: #ffbc00;
}

#about-c .about-logos {
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	grid-gap: 2rem;
}

#about-c img {
	width: 70%;
}

#contact-a .text-fields {
	display: grid;
	grid-template-areas: "name email" "subject phone" "message message";
	grid-gap: 1.2rem;
	margin-bottom: 1.2rem;
}
#contact-a .text-fields input {
	border: none;
	background-color: #ccc;
}
#contact-a .text-fields .name-input {
	grid-area: name;
}
#contact-a .text-fields .email-input {
	grid-area: email;
}
#contact-a .text-fields .subject-input {
	grid-area: subject;
}
#contact-a .text-fields .phone-input {
	grid-area: phone;
}
#contact-a .text-fields .message-input {
	grid-area: message;
	height: 100px;
}
#contact-a .text-fields .text-input {
	padding: 0.5rem 1rem;
	border: none;
	background-color: #ccc;
}

#contact-a button[type="submit"] {
	width: 50%;
	margin-bottom: 2rem;
}

#contact-b .contact-info {
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	text-align: center;
	color: white;
}

#contact-c h1 {
	text-align: center;
	font-size: 4rem;
	color: black;
}

#contact #recap {
	display: flex;
	align-items: center;
}

#main-footer {
	background: rgba(2, 0, 31, 0.87);
	color: white;
	height: 5rem;
}
#main-footer .footer-content {
	display: flex;
	justify-content: space-between;
	height: 5rem;
	align-items: center;
}
#main-footer .social .fab {
	margin-right: 1rem;
	border: 2px white solid;
	border-radius: 50%;
	height: 20px;
	width: 20px;
	line-height: 20px;
	text-align: center;
	padding: 0.5rem;
	color: #fff;
}
#main-footer .social .fab:hover {
	background: #ffbc00;
}

@media (max-width: 800px) {
	#header-home {
		height: 30rem;
	}
	.header-content {
		padding-top: 5rem;
	}
}

@media (max-width: 500px) {
	#main-nav {
		flex-direction: column;
		align-items: center;
	}
	#main-nav li {
		padding: 1rem;
	}
	#header-home {
		height: 10rem;
		border-bottom: 3px solid #ffbc00;
		background-position: 20% 30%;
	}
	#header-home .header-content {
		display: none;
	}
	#header-inner {
		height: 10rem;
	}
	#home-a .specials,
	#home-b .stats,
	#home-c .process,
	#contact-b .contact-info,
	.items {
		grid-template-columns: repeat(1, 1fr);
	}
	#home-a .specials div {
		border-bottom: 1px #f4f4f4 solid;
		padding-bottom: 1rem;
	}
	#home-a .specials div:last-child {
		border: none;
		padding-bottom: 0;
	}
	#home-b .stats.div {
		padding: 2rem 0 1rem 0;
	}
	#about-a .about-info {
		grid-template-areas: "bioimage" "bio" "st1" "st2" "st3";
	}
	#about-c {
		display: none;
	}
	#contact-a .text-fields {
		grid-template-areas: "name" "subject" "email" "phone" "message";
	}
	#contact-b div {
		margin-bottom: 1rem;
		padding-bottom: 1rem;
		border-bottom: 1px #ccc solid;
		font-weight: 300;
	}
	#contact-b div:last-child {
		border: none;
		margin-bottom: 0;
		padding-bottom: 0;
	}
	#contact-c h1 {
		font-size: 2rem;
	}
	#main-footer {
		height: 7rem;
	}
	#main-footer .footer-content {
		flex-direction: column;
		padding: 1rem;
		height: 5rem;
	}
}

@media (max-height: 500px) {
	#header-home .header-content {
		padding-top: 3rem;
	}
}

@media (max-height: 320px) {
	#header-home .header-content h1 {
		font-size: 2rem;
	}
}
